
import api from "../config/api.js"
import Util from "../utils/utils"
import countdown from "./public/countdown.vue"
import  Velocity from 'velocity-animate'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
Toast,
countdown
},
data() {
return {
token: "",
currentRoomId: "",
points: 0,
totalPoints: 0,
city: "",
area: "",
status: "",
name: "",
num: "",
id: "",
clickAble: true,
loading: true,
tip: false,
count: 0,
showFirst: true,
showSecond: false,
showWait: false,
countDownTime: 600,
userInfo1:[{
id: 1,
text: 10000
},
{
id: 2,
text: 20000
},
{
id: 3,
text: 30000
},
{
id: 4,
text: 40000
},
{
id: 5,
text: 50000
},
{
id: 6,
text: 60000
}
],
userInfo2:[{
id: 11,
text: 11111
},
{
id: 12,
text: 21111
},
{
id: 13,
text: 31111
},
{
id: 14,
text: 41111
},
{
id: 15,
text: 51111
},
{
id: 16,
text: 61111
}
]
}
},
created() {
this.init()
},
computed: {
},
















mounted() {





},
methods: {
init() {
this.getToken()
this.getRoomId()
},
getToken() {
this.token = Util.pageUrlGetValue("token")
Util.setCookie("token", this.token, "aylives.cn")
},
getRoomId() {
this.currentRoomId = Util.pageUrlGetValue("currentRoomId")
Util.setCookie("currentRoomId", this.currentRoomId, "aylives.cn")
},
getParam() {
api.Axios.get(api.USERINFO).then(res => {
if (res.data.code === 200) {
this.area = res.data.data.agencyName
this.city = res.data.data.cityName
this.status = res.data.data.added
if (this.status) {
this.$router.push({ path: "/poster" })
}
} else {
this.$toast(res.data.msg, 1500)
}
})
},
collect (item) {
let index = item.id
let oId = document.getElementById(`id${index}`)
this.count++
console.log("count:", this.count);



Velocity(oId,{
top: 40,
opacity: 0
}, {
duration: 1000,
complete: function() {

}
})


Util.sound()





this.refresh(item.text)

if (this.count === 6) {
this.showFirst = false
this.showSecond = true
}
if (this.count === 12) {
this.showSecond = false
this.showWait = true
}
},
refresh (point) {
this.points += point
},
jumpToRank () {
this.$router.push({ path: "/rank"})
}
}
};



